@charset "UTF-8";
:root {
  --primary-yellow: #C2A242;
  --primary-yellow-light: #FFFFCC;
  --light-yellow: #FAE78D;
  --light-yelloww: #FFF9C4;
  --dark-yellow: #D4B72E;
  --very-light-yellow: #FEF9E7;
  --medium-yellow: #E5C349;
  --golden-yellow: #B89720;
  --teste-1: #FFE47A;
  --teste-2: #FFEDA5;
  --teste-3: #E1BD2E;
  --teste-4: #B49414;
  --teste-5: #E1BD2E;
  --teste-6: #B49414;
  --teste-7: #FFE47A;
  --light-gray: #F0F0F0;
  --medium-gray: #B0B0B0;
  --dark-gray: #333333;
  --light-green: #C5E1A5;
  --dark-green: #388E3C;
  --lime-green: #DFF3E3;
  --forest-green: #2C6E49;
  --light-blue: #A7C7E7;
  --dark-blue: #2A4D69;
  --primary-blue: #007A78;
  --black-light: #1A1A1A;
  --black-lighten: #192033;
  --black-normal: #010101;
  --black-dark: #000000;
  --white: #FFFFFF;
  --white-normal: #F5F5F5;
  --white-dark: #D9D9D9;
  --gray-900: #030E15;
  --gray-800: #2D3235;
  --gray-700: #4F5152;
  --gray-600: #78797A;
  --gray-500: #9A9D9E;
  --gray-400: #BABCBD;
  --gray-300: #C6C7C8;
  --gray-200: #DADCDD;
  --gray-100: #F6F6F6;
  --swiper-grey: #d3d3d3;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  list-style: none;
  text-decoration: none;
  border: 0;
  -webkit-font-smoothing: antialiased;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
  overflow-x: hidden !important;
}

body {
  -webkit-font-smoothing: antialiased;
}

main {
  position: relative !important;
}

a {
  display: inline-block;
}

svg, img {
  display: inline-block;
  max-width: 100%;
}

img {
  -o-object-fit: cover;
  object-fit: cover;
}

button, a {
  background-color: transparent;
  border: none;
  cursor: pointer !important;
}

button {
  cursor: pointer;
}

:focus {
  outline: 0;
  box-shadow: 0 0 0 2px var(--primary-green);
}

.fancybox__container {
  z-index: 999999 !important;
}

@media (max-width: 1400px) {
  .container {
    padding: 0px 25px;
  }
}
@media (max-width: 678px) {
  .container {
    padding: 0px 15px;
  }
}
h1, h2, h3, h4, h5, h6, p, span, a {
  font-family: "Roboto", sans-serif;
}

h2, h3 {
  line-height: 120%;
}

h2, h5, h6, p {
  line-height: 130%;
}

h1 {
  font-size: 40px;
  font-weight: 800;
  line-height: 100%;
  letter-spacing: -0.96px;
}

h2 {
  font-size: 40px;
  font-weight: 800;
  letter-spacing: -0.8px;
}

h3 {
  font-size: 36px;
  font-weight: 800;
  letter-spacing: -0.72px;
}

h4 {
  font-size: 28px;
  font-weight: 600;
}

h5 {
  font-size: 20px;
  font-weight: 700;
}

h6 {
  font-size: 18px;
  font-weight: 500;
}

p {
  color: var(--black-dark);
  font-size: 18px;
  font-weight: 400;
}

header {
  background-color: #0B0A0D;
  height: auto;
}
header .custom-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 10rem;
}
header .custom-header a {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
header .custom-header a img {
  width: 10rem;
  height: auto;
}
header .custom-header a p {
  color: white;
  font-weight: bold;
  font-size: 0.9rem;
  margin: 0;
}
header .custom-header nav ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 40px;
}
header .custom-header nav ul li {
  margin-left: 1rem;
}
header .custom-header nav ul li a {
  font-weight: bold;
  color: var(--primary-yellow);
  text-decoration: none;
}
header .custom-header nav ul li a:hover {
  color: var(--light-gray);
}

section {
  width: 100%;
  height: calc(100vh - 9.6rem);
}

#s__home {
  background-image: url("../assets/bg/bg.jpg");
  background-size: cover;
}
#s__home .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  gap: 2rem;
  color: var(--primary-yellow);
}
#s__home .container h1 {
  color: var(--primary-yellow-dark);
}
#s__home .container .filter {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  width: 120rem;
  padding: 1rem;
  border: 1px solid black;
  border-radius: 20px;
}
#s__home .container .filter p {
  margin-bottom: 1rem;
}
#s__home .container .filter form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 2rem;
}
#s__home .container .filter form .filters {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 5rem;
  height: auto;
}
#s__home .container .filter form .filters .filter-item {
  display: flex;
  flex-direction: column;
  align-items: right;
  justify-content: center;
  width: 100%;
  max-width: 20rem;
  text-align: left;
}
#s__home .container .filter form .filters .filter-item label {
  width: 20rem;
  display: block;
  margin-bottom: 0.8rem;
  font-weight: bold;
  font-size: 1.5rem;
}
#s__home .container .filter form .filters .filter-item input,
#s__home .container .filter form .filters .filter-item select {
  width: 100%;
  height: 30px;
  border-radius: 10px;
  padding: 0 15px;
}
#s__home .container .filter form .filters .filter-item button {
  width: 5rem;
  background-color: var(--primary-yellow);
  border: 1px solid black;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 2.5rem;
}

#s__imoveis {
  height: calc(190vh - 9.6rem);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;
}
#s__imoveis .container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
#s__imoveis .container h1 {
  margin: 40px 0;
  font-size: 3rem;
  color: var(--dark-gray);
}
#s__imoveis .container .row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 20px;
}
#s__imoveis .container .row .col-md-4 {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
#s__imoveis .container .row .card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 300px;
  border: 1px solid #ddd;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
#s__imoveis .container .row .card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
#s__imoveis .container .row .card .card-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  text-align: center;
}
#s__imoveis .container .row .card .card-body p {
  margin-bottom: 10px;
  font-size: 1.5rem;
  color: var(--lime-greeny);
}
#s__imoveis .container .row .card .card-body h5 {
  margin-bottom: 10px;
  font-size: 1.25rem;
  color: #333;
}
#s__imoveis .container .row .card .card-body .card-text {
  margin: 5px 0 20px 0;
  font-size: 1rem;
  font-weight: bold;
  color: #666;
}
#s__imoveis .container .row .card .card-body .card-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 15px;
  gap: 1rem;
}
#s__imoveis .container .row .card .card-body .card-info .info-details {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  width: 100%;
}
#s__imoveis .container .row .card .card-body .card-info .info-location {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  padding-left: 1.5rem;
}
#s__imoveis .container .row .card .card-body .card-info p {
  margin: 0;
  font-size: 0.9rem;
  color: #666;
}
#s__imoveis .container .row .card .card-body .card-info p i {
  margin-right: 5px;
  color: #333;
}
#s__imoveis .container .row .card .card-body .btn {
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
#s__imoveis .container .row .card .card-body .btn:hover {
  background-color: #0056b3;
}
#s__imoveis .container .btn {
  margin-top: 2rem;
  padding: 10px 20px;
  font-size: 2rem;
  color: var(--primary-yellow);
  background-color: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
#s__imoveis .container .btn:hover {
  background-color: var(--dark-green);
}

#s__serviços {
  height: calc(100vh - 9.6rem);
  background-color: var(--primary-yellow-light);
}
#s__serviços .container {
  padding: 5rem;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
  gap: 5rem;
}
#s__serviços .container h1 {
  font-size: 3rem;
  color: var(--primary-yellow);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
}
#s__serviços .container .content {
  display: flex;
  flex-direction: column;
  align-items: right;
  justify-content: center;
  gap: 3rem;
  width: 100%;
}
#s__serviços .container .content .row {
  gap: 2rem;
}
#s__serviços .container .content .row .card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  background-color: var(--primary-yellow-light);
  border: 2px solid var(--primary-yellow);
  border-radius: 1rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
#s__serviços .container .content .row .card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
}
#s__serviços .container .content .row .card i {
  font-size: 5rem;
  color: var(--primary-yellow);
}
#s__serviços .container .content .row .card p {
  font-size: 1.5rem;
  color: var(--primary-yellow);
  font-weight: bold;
  text-transform: uppercase;
  margin: 0;
}

#s__sobre {
  background-color: var(--primary-yellow);
  padding: 10rem;
}
#s__sobre .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#s__sobre .container h1 {
  margin: 40px 0;
  font-size: 3rem;
  color: var(--dark-gray);
}
#s__sobre .container .content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  gap: 10rem;
}
#s__sobre .container .content .left {
  width: 30%;
}
#s__sobre .container .content .left iframe {
  width: 18rem;
}
#s__sobre .container .content .right {
  width: 50%;
}